@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Megrim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.image-stack {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
}

.image-stack_item_top_g1 {
  grid-row: 1;
  margin-top: 20%; 
  z-index: 1;
}

.image-stack_item_bottom_g1 {
  grid-row: 1;
}

.image-stack_item_top_g2 {
  grid-row: 1;
  margin-top: 60%; 
  z-index: 3;
}

.image-stack_item_bottom_g2 {
  margin-top: 40%; 
  grid-row: 1;
  z-index: 2;
}

.image-stack_item_top_g3 {
  grid-row: 1;
  margin-top: 100%; 
  z-index: 5;
}

.image-stack_item_bottom_g3 {
  margin-top: 80%; 
  grid-row: 1;
  z-index: 4;
}


.SubCountdown-text{
  font-family: 'Megrim', cursive;
  font-weight: bold;
  text-align: center;
  color: peru;
}

.Header-text{
  font-family: 'Amatic SC', cursive;
  font-size: xxx-large;
  font-weight: bold;
  text-align: center;
  color: burlywood;
}

.Countdown-text{
  font-family: 'Amatic SC', cursive;
  text-align: center;
  font-weight: bold;
  color: burlywood;
}

.Date-text{
  font-family: 'Poiret One', cursive;
  text-align: center;
  font-weight: bold;
  color: white;
}

.Modal-text{
  font-family: 'Amatic SC', cursive;
  font-size: xx-large;
  font-weight: bold;
  text-align: center;
  color: white;
}
