.image-stack {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  position: relative;
}

.image-stack_item_top_g1 {
  grid-row: 1;
  margin-top: 20%; 
  z-index: 1;
}

.image-stack_item_bottom_g1 {
  grid-row: 1;
}

.image-stack_item_top_g2 {
  grid-row: 1;
  margin-top: 60%; 
  z-index: 3;
}

.image-stack_item_bottom_g2 {
  margin-top: 40%; 
  grid-row: 1;
  z-index: 2;
}

.image-stack_item_top_g3 {
  grid-row: 1;
  margin-top: 100%; 
  z-index: 5;
}

.image-stack_item_bottom_g3 {
  margin-top: 80%; 
  grid-row: 1;
  z-index: 4;
}

